:root {
  --primary: hsl(345, 100%, 53%);
  --secondary: blue;
  --dark: black;
  ---light: hsl(0, 51%, 92%);
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "fira Sans", sans-serif;
}
body {
  background-color: var(---light);
}
.App {
  display: flex;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
}
.calculator {
  width: 100%;
  max-width: 400px;
  background-color: black;
  overflow: hidden;
  box-shadow: 0px 2px 64px rgba(0, 0, 0, 0.5);
  border-radius: 15px;
}
.display {
  padding: 16px;
  text-align: right;
  background-color: var(---dark);
  color: var(---light);
  font-size: 30px;
  font-weight: 300;
  color: white;
}
.display span {
  font-size: 14px;
  color: black;
}
button {
  appearance: none;
  border: none;
  outline: none;
  color: white;
  font-size: 24px;
  padding: 20px;
  cursor: pointer;
  transition: o.4s;
}
button:hover {
  opacity: 0.9;
}
.operators {
  display: flex;
}
.operators button {
  flex: 1 1 0%;
  background-color: var(--primary);
  font-weight: 700;
}
.digits {
  display: flex;
  flex-wrap: wrap;
  font-size: 20px;
}
.digits button {
  flex: 1 1 33.333%;
  max-width: 33.333%;
  background-color: var(---dark);
}
